/* CAROUSEL */

.carousel {
  position: relative;
  height: 100vh;
  overflow: hidden;
}

.carousel__items {
  z-index: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.carousel__item {
  display: inline-block;
}

.carousel__img {
  -o-object-fit: cover;
     object-fit: cover;
  width: 100vw;
  height: 100vh;
}


/* BUTTONS */

.carousel__nav{
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 100;
}

.carousel__nav-btn {
  width: 10rem;
  height: 10rem;
  background-color: #eee;
  border: 0;
  cursor: pointer;
  padding: 0;
  outline: none;
}

.carousel__nav-btn svg {
  width: 1.8rem;
  height: 1.8rem;
}

.carousel__nav-btn:hover {
  background-color: #fff;
}

.carousel__nav-btn:disabled {
  background-color: #ccc;
  cursor: default;
}

.carousel__nav-btn:first-child {
  border-top-left-radius: 2px;
  border-right: 1px solid #666;
}


/* DOTS */

.carousel__dots {
  position: absolute;
  left: 5rem;
  bottom: 5rem;
  height: 10px;
  width: 8rem;
  display: -ms-flexbox;
  display: flex;
  z-index: 100;
}

.carousel__dot {
  height: 6px;
  width: 6px;
  background-color: #333;
  border-radius: 50%;
  display: inline-block;
}

.carousel__dot:not(:last-child) {
  margin-right: 1rem;
}



